import BrowserHandler from '../common/browserHandler';

('use strict');

export const isIFrame = window.parent !== window;
export function log(message: string, severity = 'DEBUG'): void {
    var scriptfile = ((): string => {
        var stack = new Error().stack.split('\n');
        if (stack.length < 4) {
            return undefined;
        }

        var location = stack[3].match(/\b[\w.]+\b:\d+/g);
        return location.pop();
    })();

    BrowserHandler.runtime.sendMessage({
        activity: 'log',
        filename: `${isIFrame ? '(iframe)' : ''}${scriptfile || ''}`,
        severity,
        log: message,
    });
}
