/* global chrome*/
import { isIFrame, log } from './content.helper';
let SSOHelper: any = null;

if (!isIFrame && location.hostname === 'login.microsoftonline.com') {
    log(`Loaded ${location.href}`);
}

window.addEventListener(
    'message',
    function (event: MessageEvent): void {
        'use strict';
        if (event.data.channel) {
            const channelId = '53ee284d-920a-4b59-9d30-a60315b26836';
            try {
                if (event.data.channel !== channelId) {
                    log(`bad channel: ${event.data}`);
                    return;
                }

                // Only accept messages from ourself.
                if (event.source !== window) {
                    log(`invalid event.source: ${event.source}`);
                    return;
                }

                const request = event.data;
                const method = request.body.method;
                const extensionId = chrome.runtime.id;

                if (method === 'CreateProviderAsync') {
                    // Legacy communication pattern
                    const extList = document.getElementById(`ch-${channelId}`);
                    if (extList) {
                        var extElement = document.createElement('div');
                        extElement.id = extensionId;

                        var version = chrome.runtime.getManifest().version;
                        if (version) {
                            extElement.setAttribute('ver', version);
                        }

                        extList.appendChild(extElement);
                    }
                    return;
                } else if (!SSOHelper) {
                    // Create helper function. They're not loaded in global because they're not needed in every page (optimization).
                    // However, they're stored in global.
                    SSOHelper = {
                        Call: function (request: any, port: any): void {
                            try {
                                chrome.runtime.sendMessage({ activity: 'sso', body: request.body }, function (response) {
                                    if (response === null || response === undefined) {
                                        const description = chrome.runtime.lastError ? chrome.runtime.lastError.message : 'No response';

                                        SSOHelper.Callback(request, {
                                            status: 'Fail',
                                            code: 'ContentError',
                                            description,
                                        }, port);

                                        return;
                                    }
                                    SSOHelper.Callback(request, response, port);
                                });
                            } catch (e) {
                                SSOHelper.Callback(request, {
                                    status: 'Fail',
                                    code: 'ContentError',
                                    description: e.toString(),
                                }, port);
                            }
                        },

                        Callback: function (request: any, response: any, port?: MessagePort): void {
                            const req = {
                                channel: channelId,
                                extensionId: extensionId,
                                responseId: request.responseId,

                                body: {
                                    method: 'Response',
                                    response: response,
                                },
                            };

                            log(`SSO response status: ${JSON.stringify(response.status)}`, response.status === 'Fail' ? 'ERROR' : 'DEBUG');
                            if (port) {
                                log('Sending response over MessageChannel port');
                                port.postMessage(req);
                            } else {
                                log('Sending response over window.postMessage');
                                window.postMessage(req, location.origin);
                            }
                        },
                    };
                }
                
                if (method === "Handshake" && (!request.extensionId || request.extensionId === extensionId) && event.ports && event.ports.length) {
                    // Stop event propagation to prevent caller and other extensions from reacting to this event
                    event.stopImmediatePropagation();
                    log('Received Handshake request, sending HandshakeResponse');
                    var req  = {
                        channel: channelId,
                        extensionId: extensionId,
                        responseId: request.responseId,
                        body: {
                            method: "HandshakeResponse",
                            version: chrome.runtime.getManifest().version
                        }
                    };

                    var port = event.ports[0];
                    port.onmessage = (event: MessageEvent): void => {
                        var request = event.data;
                        SSOHelper.Call(request, port);
                    };
                    port.postMessage(req);
                } else if (request.extensionId === extensionId && method !== "Response") {
                    // Legacy communication pattern
                    SSOHelper.Call(request);
                } else {
                    log(`method: ${method} not handled, location: ${location.href}`);
                }
            } catch (e) {
                log(`caught exception: ${JSON.stringify(e)}`, 'INFO');

                // Swallow exception to not break page excecution.
            }
        }
    },
    true
); // "true" is important to give priority to the content script.
