declare let browser: any;
declare let chrome: any;

var browserListeners: any[] = [];
var propertyList = {};

function mergeProperties(target: any, source: any): any {
    for (const [key, value] of Object.entries(source)) {
        if (!target[key]) {
            target[key] = value;
            continue;
        }

        if (typeof value !== typeof target[key]) {
            console.log(`skipping ${key} due to conflicting types`);
            continue;
        }

        if (Array.isArray(value)) {
            target[key] = target[key].concat(value);
            continue;
        }

        if (typeof value === 'object') {
            target[key] = mergeProperties(target[key], value);
            continue;
        }
        target[key] = value;
    }

    return target;
}

export default (function (): any {
    var browserHandler = ((): any => {
        if (typeof browser === 'object') {
            var edgeBrowser = Object.create(browser);

            edgeBrowser.notifications = {
                clear: function (notificationId: string, callback: (wasCleared: boolean) => void): void {
                    // Invoke callback with wasCleared == false
                    callback(false);
                },
                create: function (
                    notificationId: string,
                    options: chrome.notifications.NotificationOptions,
                    callback: (notificationId: string) => void
                ): void {
                    // Use window alert dialog to display notification message
                    if (typeof options !== 'undefined' && options !== null) {
                        // Only show when priority is 1 or 2
                        if (typeof options.priority !== 'undefined' && options.priority !== null && options.priority > 0) {
                            window.alert(options.message);
                        }
                    }

                    // Invoke callback with notification id
                    callback(notificationId);
                },
                onButtonClicked: {
                    addListener: function (notificationId: string): void {
                        // Not implemented
                    },
                },
                onClicked: {
                    addListener: function (notificationId: string): void {
                        // Not implemented
                    },
                },
            };

            edgeBrowser.runtime = {
                getPlatformInfo: function (callback: (platformInfo: chrome.runtime.PlatformInfo) => void): void {
                    // Does not execute the callback function
                    // Not implemented
                },
            };
        } else if (typeof chrome === 'object') {
            return Object.create(chrome);
        }
    })();

    // when running in tests, provide override the necessary functions
    if ((window as any).jasmine) {
        const browserMock = {
            mock: true,
            browserAction: {
                setIcon: function (icon: string): void {},
            },
            extension: {
                getURL: function (name: any): string {
                    return '';
                },
            },
            i18n: {
                getUILanguage: function (name: any, callback: any): string {
                    return '';
                },
                getMessage: function (): string {
                    return '';
                },
            },
            runtime: {
                onMessage: {
                    addListener: function (listener: any): any {
                        browserListeners.push(listener);
                    },
                    removeListener: function (listener: any): void {
                        browserListeners.splice(browserListeners.indexOf(listener), 1);
                    },
                },
                sendMessage: function (message: any): any {
                    for (let i = 0; i < browserListeners.length; ++i) {
                        browserListeners[i](message);
                    }
                },
                id: 'ndjpnladcallmjemlbaebfadecfhkepb',
            },
            storage: {
                local: {
                    get: function (name: string, callback: (arg0: { [x: number]: any }) => void): any {
                        callback({
                            [name]: (propertyList as any)[name],
                        });
                    },

                    set: function (data: { [x: string]: any; hasOwnProperty: (arg0: string) => any }): void {
                        for (const key in data) {
                            if (data.hasOwnProperty(key)) {
                                (propertyList as any)[key] = /*JSON.stringify*/ data[key];
                            }
                        }
                    },

                    remove: function (name: string | number, callback: (arg0: any) => void): void {
                        delete (propertyList as any)[name];
                        callback(propertyList);
                    },

                    clear: function (name: any, callback: any): void {
                        propertyList = {};
                    },
                },
            },
            tabs: {
                create: function (createProperties: any, callback: (arg0: {}) => void): void {
                    if (callback) callback({});
                },
            },
        };
        browserHandler = mergeProperties(browserHandler || {}, browserMock);
        if (!(window as any).chrome) {
            (window as any).chrome = browserHandler;
        }
    }

    if (!(window as any).BrowserHandler) {
        (window as any).BrowserHandler = browserHandler;
    }

    return browserHandler;
})();
